/* Medium devices (desktops, 992px and up) */
@media (min-width: @screen-md-min) {
	//
	// General
	// --------------------------------------------------
	.container-theme {
		width: 960px;
	}
	.container-menu {
		width: 960px;
	}



	// Alternate navbars
	// --------------------------------------------------
	// Default navbar
	.navbar-theme {
		text-align: normal;

		.btn-theme {
			float: right;
			margin: 8px 0 0 0;
		}

		.navbar-nav {
    > li > a {
      padding: 10px;
    	}
    }

	}



	//
	// Promo2
	// --------------------------------------------------
  .promo1 {



    //
    // Список - параметры часов
    // --------------------------------------------------
    .clock-option-list {
      padding: 0;
      text-align: right;
      background: none;
      text-shadow: 0 1px 2px #000;
    }

  }

		//
	  // Gallery
	  // --------------------------------------------------
	  .gallery {

	    li {
	      list-style-type: none;
	      border:5px solid #fff;
				float: left;

	      img {
	      	width: auto;
	        .img-responsive();
	        height: 320px;
	      }

	    }

	  }



	//
  // План работы
  // --------------------------------------------------
  .plan {
    &:before {

      width: 865px;
      height: 6px;

    }

    li {

      h3 {
        &:before {        
          .size(70px, 70px);
        }
      }      

      &:nth-child(1) {

      	top: 175px;
      	left: 0;

        h3:before {
        	left: -15px;
					top: -100px;
        }
				
      }

      &:nth-child(2) {
      	left: 121px;
      	top: -45px;
      	
        h3:before {
					top: 121px;
					left: 90px;
        }

      }

      &:nth-child(3) {
      	left: 337px;
      	top: 175px;

        h3:before {
					top: -100px;
					left: 90px;
        }
      }

      &:nth-child(4) {
      	left: 554px;
      	top: -45px;

        h3:before {
					top: 121px;
					left: 90px;
        }
      }

      &:nth-child(5) {
				left: 722px;
      	top: 175px;

        h3:before {
					top: -100px;
					left: 140px;
        }
      }


    }
    
  }



	//
  // ОСТАЛИСЬ ВОПРОСЫ?
  // --------------------------------------------------
  .more-questions {
    h3 {
      font-size: 70px;
    }
		.mq-link {
			font-size: 26px;
			margin-top: 30px;
		}
  }



//
// Pre - footer
// --------------------------------------------------
.pre-footer {


	.tel {
    font-size: 40px;
  }
  
}

/* / Medium devices (desktops, 992px and up) */
}