//
// General
// --------------------------------------------------
body {
  background: #000;
  color: #fff;
  font-size: 14px;
  padding-top: 40px;
}

.container-theme {
  margin: 0 auto;
  padding: 0 15px;
}
.container-menu {
  margin: 0 auto;
  padding: 0 15px;
}
.red {
  color: #DE0A21;
}



//
// Header
// --------------------------------------------------
.land-header {
  font-family:'PF Din Text Cond Pro Light';
  padding-top: 20px;
  .contact {
    margin-top: 20px;
  }
  .logo {
    .img-responsive();
  }
  .desc {
    display: block;
    font-size: 15px;
    line-height: 16px;
  }
  .tel {
    display: block;
    font-size: 34px;
    line-height: 35px;
  }
}




//
// Promo1
// --------------------------------------------------
.promo1 {
  margin-top: 20px;
  font-family:'PF Din Text Cond Pro Light';
  background-image: url('../img/tagheuer_header.jpg');
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: auto auto;
  padding-bottom: 25px;

  
  //
  // Заголовок
  // --------------------------------------------------
  h1 {
    text-align: right;
    font-weight: bold;
    [class^='hrow'] {
      display: inline-block;
      background: #2C2C2C;
      margin: 5px 0;
      padding: 2px 5px;
    }
    .hrow1 {
      font-size: 30px;
    }
    .hrow2 {
      font-size: 32px;
    }
  }



  .block-left {
    margin:0 0 15px 0;
  }



  //
  // Список - параметры часов
  // --------------------------------------------------
  .clock-option-list {
    margin: 0;
    padding: 0 5px 0 0;
    text-align: right;
    background: rgba(0, 0, 0, 0.65);
    li {
      list-style-type: none;
      font-size: 20px;
    }
    .glyphicon {
      font-size: 15px;
      margin-left: 5px;
    }
  }



  //
  // Timer внешний блок
  // --------------------------------------------------
  .timer-block {
    background: url('../img/point-bgr.png');
    padding: 10px;
    border-radius: 15px;
    position: relative;
    margin-top: 25px;
    height: 149px;
    max-width: 375px;
    .center-block();



    //
    // Заголовок
    // --------------------------------------------------
    .title {
      padding-left: 95px;
      padding-bottom: 5px;
    }
    .line1 {
      color: #DE0A21;
      display: block;
      font-size: 20px;
    }
    .line2 {
      color: #000;
      display: block;
      font-size: 18px;
    }
    

    
    //
    // Картинка часов
    // --------------------------------------------------
    .img-clock {
      position: absolute;
      z-index: 10;
      left: -15px;
      top: -32px;
      height: 169px;
      .rotate(38deg);
    }
    


    //
    // Циферблат
    // --------------------------------------------------
    .timer {
      width: 275px;
      height: 84px;
      background: url('../img/timer.png') no-repeat 0 0;  
      position: relative;
      z-index: 9;
      float: right;
      span {
        font-family:'PF Din Text Cond Pro';
        display: block;
        position: absolute;
        font-size: 48px;
        line-height: 63px;
        top: 0;
        font-weight: bold;
        color: #6D3216;
      }
      .days {
        left: 16px;
      }
      .hours {
        left: 83px;
      }
      .minutes {
        left: 148px;
      }
      .seconds {
        left: 213px;
        color: #DE0A21;
      }
    }

  }

}




  //
  // Форма 1
  // --------------------------------------------------
  .feedback1 {
    background: url('../img/point-bgr.png');
    border-radius: 15px;
    color: #000;
    padding: 10px 0;
    max-width: 375px;
    position: relative;
