//
// Общие слили для форм
// --------------------------------------------------
.form-icon-style() {
  background-position: 13px center;
  background-repeat: no-repeat;
  padding-left: 45px;
}
.form-icon-man {
  .form-icon-style();
  background-image: url('../img/icon-name.png');
}
.form-icon-tel {
  .form-icon-style();
  background-image: url('../img/icon-phone.png');
}
.form-control {
  font-family:'PF Din Text Cond Pro Light';
  border-radius: 0px;
  border-color: #919191;
  box-shadow: 0 0 5px #c4c4c4 inset;
  font-size: 15px;
}

.btn-theme {
  color: #fff;
  border-color: darken(#FD0000, 7%);
  box-shadow: 0 0 5px -1px rgba(0, 0, 0, 0.5);
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.5);

  #gradient > .vertical(@start-color: #FD0000; @end-color: #CE0000;);
  
  &:hover,
  &:focus,
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: #fff;
    @btnstart: darken(#FD0000,10%);
    @btnend: darken(#CE0000,10%);

    #gradient > .vertical(@start-color: @btnstart; @end-color: @btnend;);
    border-color: lighten(#FD0000, 12%);
  }
}

.btn-yellow {
  color: #fff;
  border-color: #FF9400;
  box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.5);
  #gradient > .vertical(@start-color: #FFCD00; @end-color: #FFA500;);
  
  &:hover,
  &:focus,
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: #fff;
    @btnstart: lighten(#FFCD00,5%);
    @btnend: lighten(#FFA500,5%);

    #gradient > .vertical(@start-color: @btnstart; @end-color: @btnend;);
    border-color: lighten(#FF9400, 5%);
  }
}


// Alternate navbars
// --------------------------------------------------
// Default navbar

.navbar-theme {
  background-color: #2B2A29;
  border-color: #2B2A29;
  min-height: 30px;
  border-radius: 0;
  margin-bottom: 0;
  z-index: 15000;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;

  .navbar-text {
    color: @navbar-default-color;
  }

  .navbar-nav {
    > li > a {
      color: #fff;
      line-height: 18px;
      padding: 10px;

      &:hover,
      &:focus {
        color: #fff;
        background-color: darken(#2B2A29, 10%);
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        color: #fff;
        background-color: darken(#2B2A29, 10%);
      }
    }
    > .disabled > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-default-link-disabled-color;
        background-color: @navbar-default-link-disabled-bg;
      }
    }
  }

  .navbar-toggle {
    border-color: #fff;
    &:hover,
    &:focus {
      background-color: #000;
    }
    .icon-bar {
      background-color: #fff;
    }
  }

  .navbar-collapse,
  .navbar-form {
    border-color: @navbar-default-border;
  }

  // Dropdown menu items
  .navbar-nav {
    // Remove background color from open dropdown
    > .open > a {
      &,
      &:hover,
      &:focus {
        background-color: @navbar-default-link-active-bg;
        color: @navbar-default-link-active-color;
      }
    }

    @media (max-width: @grid-float-breakpoint-max) {
      // Dropdowns get custom display when collapsed
      .open .dropdown-menu {
        > li > a {
          color: @navbar-default-link-color;
          &:hover,
          &:focus {
            color: @navbar-default-link-hover-color;
            background-color: @navbar-default-link-hover-bg;
          }
        }
        > .active > a {
          &,
          &:hover,
          &:focus {
            color: @navbar-default-link-active-color;
            background-color: @navbar-default-link-active-bg;
          }
        }
        > .disabled > a {
          &,
          &:hover,
          &:focus {
            color: @navbar-default-link-disabled-color;
            background-color: @navbar-default-link-disabled-bg;
          }
        }
      }
    }
  }


  // Links in navbars
  //
  // Add a class to ensure links outside the navbar nav are colored correctly.

  .navbar-link {
    color: @navbar-default-link-color;
    &:hover {
      color: @navbar-default-link-hover-color;
    }
  }

  .btn-link {
    color: @navbar-default-link-color;
    &:hover,
    &:focus {
      color: @navbar-default-link-hover-color;
    }
    &[disabled],
    fieldset[disabled] & {
      &:hover,
      &:focus {
        color: @navbar-default-link-disabled-color;
      }
    }
  }

  .btn-theme {
    padding-top: 1px;
    padding-bottom: 1px; 
    width: 130px;
    font-size: 12px;
    margin: 7px 0;
  }
}



