  //
  // Gallery
  // --------------------------------------------------
  .gallery {
    margin: 0;
    padding: 0;
    
    .video {
      position:relative;
      padding-bottom:56.25%;
      padding-top:30px;
      height:0;
      overflow:hidden;
      border:1px solid #ccc;

      iframe, object, embed {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
      }
    }

    a {
      position: relative;
      display: block;

      &:before {
        .transition(opacity 0.5s);
        display: block;
        content: " ";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(175, 211, 238, 0.5);
        background-image: url('../img/zoom.png');
        background-repeat: no-repeat;
        background-position: center center;
        opacity: 0;
      }
      &:hover:before {
        opacity: 1;
      }
    }

    li {
      list-style-type: none;
      border:5px solid #fff;
      img {
        width: 100%;
      }
    }

  }



