/* Small devices (tablets, 768px and up) */
@media (min-width: @screen-sm-min) {
	//
	// General
	// --------------------------------------------------
	.container-theme {
		width: 750px;
	}
	.container-menu {
		width: 750px;
		padding: 0;
	}


	//
	// Header
	// --------------------------------------------------
	.land-header {
		
		.contact {
			float: right;
			margin-top: 0;
		}
		
		.logo {
		    max-width: 250px
		}

		.tel {
			font-weight: bold;
		}
	}




	//
	// Promo1
	// --------------------------------------------------
	.promo1 {
		background-size: auto 100%;
		padding-bottom: 40px;

		.promo-body {
			float: right;
		}

	  h1 {
	    .hrow1 {
	      font-size: 36px;
	    }
	    .hrow2 {
	      font-size: 38px;
	    }
	  }
		


		 //
	  // Timer внешний блок
	  // --------------------------------------------------
	  .timer-block {
		  //
	    // Картинка часов
	    // --------------------------------------------------
	    .img-clock {
	      position: absolute;
	      z-index: 10;
	      left: -15px;
	      top: -11px;
	      .rotate(0deg);
	    }
	  }

	  .block-left {
	    margin:0 15px 0 0;
	    float: left;
	    width: 375px;
	  }
	  .block-right {
	  	float: left;
	  	width: 320px;
	  }

	}



	//
	// Форма 1
	// --------------------------------------------------
	.feedback1 {
		
		//Красная стрелка
	  .arrow {
	  	display: block;
	    left: -95px;
	    bottom: -2px;
	    .rotate(0deg);
	  }
	}



	// Alternate navbars
	// --------------------------------------------------
	// Default navbar
	.navbar-theme {
		text-align: center;

		.navbar-nav {
    > li > a {
      padding: 7px;
    	}
    }

	}



	//
	// Promo2
	// --------------------------------------------------

	.promo2 {
		.clearfix();
	  h2 {
	    font-size: 43px;
	    margin: 40px 0;
	  }
	}
	


		//
	  // Gallery
	  // --------------------------------------------------
	  .gallery {

	    li {
	      list-style-type: none;
	      border:5px solid #fff;
				float: left;

	      img {
	      	width: auto;
	        .img-responsive();
	        height: 247px;
	      }

	      &:nth-child(1) {
	        width: 100%;
	        border-radius: 5px 5px 0 0;
	        img {
	        	height: auto;
	        }
	        
	      }
	      &:nth-child(2) {
	        width: 55%;
	      }
	      &:nth-child(3) {
	        width: 45%;
	      }
	      &:nth-child(4) {
	        width: 40%;
	        border-radius: 0 0 0 5px;
	      }
	      &:nth-child(5) {
	        width: 60%;
	        border-radius: 0 0 5px 0;
	      }

	    }

	  }



	//
	// Promo3
	// --------------------------------------------------
	.promo3 {

		h2 {
	    font-size: 43px;
	    margin: 40px 0;
	  }

		.promo3-right {
	    float: right;
	  }

	  p {
	  	padding-left: 40px;
	  }
	}



	//
	// Promo4
	// --------------------------------------------------
	.promo4 {
		
		h2 {
	    font-size: 43px;
	    margin: 40px 0;
	  }

	}
		//
	  // Масштабируемые блоки
	  // --------------------------------------------------
	  .scale-blocks {
	    
	    //Блок
	    li {
	      background-image: url('../img/feature-hex-bg.png');
	      background-position: center center;
	      background-size: auto auto;
	      height: 215px;
	      margin-bottom: 35px;
	      padding-top: 53px;
	      .make-sm-column(3);

	      &:nth-child(4) {
	        .line3 {
	          font-size: 25px;
	        }
	      }

	    }

	  }



	//
	// Promo5
	// --------------------------------------------------
	.promo5 {

		h2 {
	    font-size: 43px;
	    margin: 40px 0 60px 0;
	  }

	  .container-theme {
	  	overflow: hidden;
	  }

	}
	  //
	  // План работы
	  // --------------------------------------------------
	  .plan {
	    position: relative;
			height: 350px;
	    &:before {
	      content: " ";
	      display: block;
	      position: absolute;
				#gradient > .vertical(@start-color: #A2A2A2; @end-color: #C8C8C8;);

	      width: 690px;
	      height: 6px;
	      

	      left: 30px;
	      top: 130px;
	    }

	    li {
				width: 250px;
				position: absolute;
				text-align: center;

	      h3 {
	        padding-left: 0px;
	        margin: 20px 0 0px;

	        &:before {         
	          .size(60px, 60px);
	        }
	      }
	      
	      p {
	        padding-left: 0px;
	      }
	      

	      &:nth-child(1) {

	      	top: 175px;
	      	left: 0;

	        h3:before {
	        	left: -15px;
						top: -94px;
	        }
					
	      }

	      &:nth-child(2) {
	      	left: 73px;
	      	top: -45px;
	      	
	        h3:before {
						top: 127px;
						left: 90px;
	        }

	      }

	      &:nth-child(3) {
	      	left: 260px;
	      	top: 175px;

	        h3:before {
						top: -94px;
						left: 90px;
	        }
	      }

	      &:nth-child(4) {
	      	left: 427px;
	      	top: -45px;

	        h3:before {
						top: 127px;
						left: 90px;
	        }
	      }

	      &:nth-child(5) {
					left: 512px;
	      	top: 175px;

	        h3:before {
						top: -94px;
						left: 163px;
	        }
	      }


	    }
	    
	  }



    //
	  // Что говорят наши клиенты
	  // --------------------------------------------------
	  .say_header {

	    img {
	      margin-right: 10px;
	      width: auto;
	    }

	    .quote {
	      font-size: 22px;
	      line-height: 23px;
	    }

	    q {
	      quotes: "\00ab" "\00bb";
	    }

	  }
	  .say_body {
	    p {
	    	padding-left: 10px;
	    	padding-right: 10px;
	    }
	    .say_who {
	    	padding-left: 0;
	    }
	  }



	  //
	  // ТРОЙНАЯ ГАРАНТИЯ
	  // --------------------------------------------------
	  .warranty-list {
	    font-size: 27px;
	  }



	  //
	  // ЧАСТО ЗАДАВАЕМЫЕ ВОПРОСЫ
	  // --------------------------------------------------
	  .faq {
	  	h3 {
	  		font-size: 30px;	
	  		&:before {
	  			font-size: 24px;
	  			top:5px;
	  		}
	  	}
	  }



	  //
	  // ОСТАЛИСЬ ВОПРОСЫ?
	  // --------------------------------------------------
	  .more-questions {
	  	margin-top: 20px;
	    h3 {
	      font-size: 60px;
	    }
			.mq-link {
				font-size: 21px;
				margin-top: 30px;
			}
	  }



//
// Pre - footer
// --------------------------------------------------
.pre-footer {

	[class^="col"] {
    margin-bottom: 0px;
  }
}



//
// Modal
// --------------------------------------------------
.feed_back_win {
  .modal-dialog {
    width: 400px;
  }
}
.info_win {
  .modal-dialog {
    width: 500px;
  }
}

.modal-inner {
  padding: 10px 35px 20px 35px;

  .close {
  	margin-left: -25px;
  }

}




/* End Small devices (tablets, 768px and up) */
}